body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-item {
    font-size: 5em;
    color: white;
    background-color: darkslategray;
    margin-top: .25em;
    padding: .25em;
    border-radius: .5em;
    cursor: pointer;
}

.menu-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.monster-viewer-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 98%
}

.mvh-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.mvh-left-margin {
    margin-left: 10px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: rgb(39, 39, 39);
    min-width: 50%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
.monster-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: antiquewhite;
    overflow-y: scroll;
    padding: 10px;
    max-height: 90vh;
    border-radius: .5em;
}

.monster-form-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
}

.number-size {
    width: 3em;
}

.textarea-size {
    width: 25em;
    height: 5em;
}

.column-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.row-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
}

.row-container-space-between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.center-container {
    justify-content: center;
    align-items: center;
}

.monster-form-row-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 10px;
}

.left-margin {
    margin-left: 10px;
}

.mm-align-left {
    text-align: left;
}

.side-margin {
    margin-left: 10px;
    margin-right: 10px;
}

.top-margin {
    margin-top: 10px;
}

.border-bottom {
    border-bottom: 2px solid darkslategray;
}

.red-text {
    color: darkred;
}

.bold-text {
    font-weight: bold;
}

.mv-view {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.mv-monster {
    background-color: darkslategray;
    color: white;
    width: 12em;
    margin-top: .25em;
    margin-bottom: .25em;
    padding: .25em;
    border-radius: .5em;
    cursor: pointer;
}
.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
}

